* {
  color: #112A46;
}

.card-body {
  background: linear-gradient(130deg, rgb(172, 200, 229) 35%, rgb(99, 210, 232) 100%);
  border-radius: 2%;
  border-style: none;
  box-shadow: 0px 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.button {
  box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.2), 3px 2px 2px 0 rgba(0, 0, 0, 0.19);
}